import React from 'react';
import css from "./HelpFormSection.module.scss";
import SwitchOrg from "../../../SwitchOrg/SwitchOrg";
import FormInput from "../../../FormInput/FormInput";

const HelpFormSection = props => {
    return (
        <>
            <h1 className={css.h1}> </h1>
            <SwitchOrg switchClass={css.switch} defClass={css.switchBtn} activeClass={css.switchBtnActive}/>
            <div className={css.fromContent}>
                <div className={css.col}>
                    <div className={css.doubledInput}>
                        <FormInput name={'name'} labelname={"'"}/>
                        <FormInput name={'surname'} labelname={''}/>
                    </div>
                    <FormInput name={'company'} labelname={' , '}/>
                    <FormInput name={'email'} labelname={'Email-'}/>
                    <FormInput name={'tel'} labelname={' '}/>
                    <div className={css.bankInfo}>
                        <FormInput name={'legalAddress'} labelname={' '}/>
                        <div className={css.doubledInput}>
                            <FormInput name={'bankName'} labelname={'  '}/>
                            <FormInput name={'mfi'} labelname={' '}/>
                        </div>
                        <FormInput name={'currentAcc'} labelname={' '}/>
                    </div>
                </div>
                <div className={css.col}>
                    <FormInput name={'country'} labelname={''}/>
                    <div className={css.doubledInput}>
                        <FormInput name={'city'} labelname={""}/>
                        <FormInput name={'region'} labelname={', '}/>
                    </div>
                    <FormInput name={'address'} labelname={''}/>
                    <FormInput name={'postCode'} labelname={' '}/>
                    <div className={css.doubledInput}>
                        <FormInput name={'zkpo'} labelname={""}/>
                        <FormInput name={'ipTax'} labelname={' '}/>
                    </div>
                    <div className={css.doubledInput}>
                        <FormInput name={'itn'} labelname={""}/>
                        <FormInput name={'taxSys'} labelname={' '}/>
                    </div>
                    <div className={css.doubledInput}>
                        <FormInput name={'accountingTel'} labelname={" "}/>
                        <FormInput name={'fax'} labelname={''}/>
                    </div>
                </div>
            </div>
        </>
    );
};

export default HelpFormSection;